body {
  margin: 0 30px;
  font-family: Times New Roman, Times, serif;
}

.bg_search-form {
  height: 55px;
  background-color: #ba8cb1;
  border-radius: 7px;
  margin-bottom: 30px;
}

.search-form {
  justify-content: center;
  padding-top: 16px;
  display: flex;
}

input {
  width: 240px;
  height: 25px;
}

form button {
  width: 80px;
  border-radius: 5px;
  margin-left: 10px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  display: flex;
}

.photo-card {
  border: 2px solid #c28d8d;
  border-radius: 6px;
}

.photo-card img {
  width: 400px;
}

.info {
  padding: 8px;
  display: flex;
}

.info-item {
  margin: 0;
  padding: 4px;
}

.load-more {
  cursor: pointer;
  border: 3px dashed #a7516e;
  border-radius: 10px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 25px;
  display: block;
}

